.map__image {
  width: 100%;
  height: 530px;
  position: relative;
  padding: 0 100px;

  @media (max-width: $medium-width) {
    padding: 0 14px;
  }
}
.map__goole-map {
  position: absolute;
  width: inherit;
  height: 530px;
}

.map__image-png {
  display: block;
  width: 100%;
  height: 530px;
  object-fit: cover;
}
