.about-us__wrapper {
  padding: 128px 0;

  @media (max-width: $medium-width) {
    padding: 70px 90px 24px;
  }

  @media (max-width: $small-width) {
    padding: 70px 0 24px;
  }
}

.about-us__title {
  margin: 18px auto 48px;
  text-align: center;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  color: $basic-content;

  @media (max-width: $medium-width) {
    margin: 0 auto 30px;
    font-weight: 600;
    font-size: 24px;
    line-height: 41px;
  }
}

.about-us__list {
  @include reset-list;
  @include display-flex-wrap;
  list-style-type: none;
  counter-reset: num;
  justify-content: space-between;
}

.about-us__item {
  position: relative;
  width: 23%;
  min-height: 70px;
  padding-top: 34px;
  font-size: 22px;
  line-height: 34px;
  text-align: center;

  @media (max-width: $medium-width) {
    margin-bottom: 20px;
    width: 47%;
  }

  @media (max-width: $small-width) {
    margin-bottom: 20px;
    width: 100%;
    font-size: 18px;
    line-height: 34px;
  }
}

.about-us__item::before {
  content: "0"counters(num, "");
  counter-increment: num;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 44px;
  width: 100px;
  height: 60px;
  font-weight: 700;
  font-size: 28px;
  line-height: 38px;
  color: $accent-main;
  text-align: right;

  @media (max-width: $small-width) {
    left: 62px;
  }
}
